.my-custom-button {
    width: 100%;
    border-radius: 0px;
    background-color: rgb(76, 131, 220);
    padding: 5px;
    color: rgb(38, 39, 40);
    height: 45px;
}

.button-container {
    display: flex;
    align-items: center;  /* Align button and icon vertically */
}

.shimmer-effect {
    background: linear-gradient(-45deg, #00BFFF, #f07816); /* Gemini Blue to Red */
    background-size: 400% 400%;
    animation: shimmer 1s ease infinite;
  }
  
  @keyframes shimmer {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
  .my-custom-button.shimmer-effect {
    /* You might need to adjust button styles for the effect to look good */
    border: none; /* Remove default button border */
    color: transparent; /* Hide the text while shimmering */
  }

  .button-container {
    display: flex; /* Flexbox 적용 */
    justify-content: flex-end; /* 오른쪽 정렬 */
  }
  
  .my-custom-button {
    display: flex; /* 버튼 내부 요소들을 flexbox로 배치 */
    align-items: center; /* 수직 가운데 정렬 */
  }
  
  .my-custom-button span {
    margin-right: auto; /* 텍스트와 이미지 사이 공간 확보 */
  }

  .my-custom-button {
    display: flex;
    align-items: center; /* 수직 가운데 정렬 유지 */
    justify-content: space-between; /* 텍스트와 이미지 사이 공간 최대화 */
  }
  
  .my-custom-button span {
    margin: 0 auto; /* 텍스트를 중앙으로 정렬 */
  }

  .my-custom-button.shimmer-effect span {
    color: black !important; 
  }

